import { useState } from "react";
import { gameData } from "../../utils/gameData";
import styles from "./Pane.module.scss";
import { tokens } from "@fluentui/react-components";

const Pane = ({ setSelectedGame }) => {
  const [chosenGame, setChosenGame] = useState(null);

  const lightenColor = (color, percent) => {
    var num = parseInt(color.replace("#", ""), 16),
      amt = Math.round(2.55 * percent),
      R = (num >> 16) + amt,
      G = ((num >> 8) & 0x00ff) + amt,
      B = (num & 0x0000ff) + amt;

    return (
      "#" +
      (
        0x1000000 +
        (R < 255 ? (R < 1 ? 0 : R) : 255) * 0x10000 +
        (G < 255 ? (G < 1 ? 0 : G) : 255) * 0x100 +
        (B < 255 ? (B < 1 ? 0 : B) : 255)
      )
        .toString(16)
        .slice(1)
    );
  };

  const handleSelectedGame = (game) => {
    setSelectedGame(game);
    setChosenGame(game);
  };

  return (
    <div className={styles.root}>
      {gameData.map((game, index) => {
        return (
          <div
            key={`${game.name}-${index}`}
            style={{
              backgroundColor: game.color,
              outline:
                chosenGame?.name === game.name
                  ? `3px solid ${lightenColor(chosenGame.color, 90)}`
                  : undefined,
              boxShadow: chosenGame?.name === game.name ? tokens.shadow8 : undefined,
            }}
            className={styles.gameButton}
            role={"button"}
            onClick={() => handleSelectedGame(game)}
          >
            {game.name}
          </div>
        );
      })}
    </div>
  );
};

export default Pane;
